import { Conditionals, RulesVariables } from '../../types'
import { ExactPath } from '../../router/exact'
import toEdgeRegex from '../../utils/toEdgeRegex'
import toPathRegexp from '../../utils/toPathRegexp'
import {
  InOperatorCriteriaValue,
  InOperatorValues,
  isNegation,
  OptionalCriteriaValue,
  PathCriteriaValue,
} from '../RouteCriteria'

export const toCondition = (
  property: RulesVariables,
  value: OptionalCriteriaValue | PathCriteriaValue | InOperatorCriteriaValue
): Conditionals => {
  if (isNegation(value)) {
    if (typeof value.not === 'string') {
      if (property.request === 'path' && toPathRegexp(value.not)) {
        return { ['!=']: [property, value.not] }
      }
      // everything that isnt a path is matched exactly without the need for exact modifier
      return { ['!==']: [property, value.not] }
    } else if (value.not instanceof RegExp) {
      return { ['!~']: [property, toEdgeRegex(value.not)] }
    } else if (value.not instanceof ExactPath) {
      return { ['!==']: [property, value.not.value] }
    } else if (Array.isArray(value.not)) {
      return { not_in: [property, value.not as InOperatorValues] }
    }
  } else {
    if (typeof value === 'string') {
      if (property.request === 'path' && toPathRegexp(value)) {
        return { ['==']: [property, value] }
      }
      // everything that isnt a path is matched exactly without the need for exact modifier
      return { ['===']: [property, value] }
    } else if (value instanceof RegExp) {
      return { ['=~']: [property, toEdgeRegex(value)] }
    } else if (value instanceof ExactPath) {
      return { ['===']: [property, value.value] }
    } else if (Array.isArray(value)) {
      return { in: [property, value as InOperatorValues] }
    }
  }

  // todo: check if this is the correct error to throw, we shouldn't allow nulls/undefineds
  // so this should be handled with ts or with runtime checks on sailfish schema during build
  throw new Error(`Invalid type for match condition: ${typeof value}`)
}
