import { getConfig } from '../config'
import { PreloadConfig, PreloadRequest, PreloadOption } from './Preload'
import Tasks from '../utils/Tasks'

export default class PreloadRequests {
  /**
   * The list of preload config options which will be later resolved to PreloadRequests.
   */
  options: PreloadOption[] = []

  /**
   * Adds preload config options to the list
   * @param preloadOptions
   */
  push(...options: PreloadOption[]) {
    this.options.push(...options)
  }

  /**
   * Returns object with preload config
   * @private
   */
  async getPreloadConfig(): Promise<PreloadConfig> {
    const edgioConfig = getConfig()
    const requests: PreloadRequest[] = []

    await new Tasks()
      .add(...this.options)
      .onTaskDone((result: PreloadRequest[]) => requests.push(...result))
      .resolve()

    return {
      requests,
      concurrency: edgioConfig?.prerenderConcurrency,
    }
  }
}
