import EdgeFunctionHeaders from './EdgeFunctionHeaders'
import { Buffer } from 'buffer'

export default class EdgeFunctionResponse {
  /**
   * The HTTP status code
   */
  statusCode: number

  /**
   * The HTTP status message. It's undefined by default and if left undefined, the response
   * should set it to the default status message for the assigned status code.
   *
   * Connascence of behavior with Sailfish. See http_message.cc
   */
  statusMessage?: string

  /**
   * The response body as a string or a Buffer
   */
  body?: Buffer

  /**
   * The response headers as a dictionary of (name, value) pairs. Value can be a string
   * or an array of strings when the header appears more than once in the response.
   * E.g. multiple set-cookie: headers
   */
  headers: EdgeFunctionHeaders

  /**
   * A Response becomes immutable after return statement is called
   */
  responseReturned: boolean

  /**
   * Creates a new EdgeFunctionResponse out of any object that fulfils basic interface of a generalized Response.
   */
  constructor({
    statusCode,
    statusMessage,
    body,
    headers,
  }: {
    statusCode?: number
    statusMessage?: string
    body?: string | Buffer | Uint8Array | ArrayBuffer
    headers?: { [name: string]: string | string[] }
  }) {
    this.statusCode = statusCode || 0 // Connascence of the value in Sailfish. See http_message.h
    this.statusMessage = statusMessage
    if (Buffer.isBuffer(body) || typeof body === 'string' || body instanceof Uint8Array) {
      // Subtle issue here. We must copy the body because the buffer that the response
      // arrived in is shared by the parent/worker threads and v8.deserialize does shallow copy on buffers.
      // Hence if we don't copy it, this body will be overwritten by the next response that comes in.
      // body can be overwritten by the next response that comes in.
      this.body = Buffer.from(body)
    }
    this.headers = new EdgeFunctionHeaders(headers)
    this.responseReturned = false
  }

  // Reads the body chunk. Returns an undefined if the data cannot be read.
  readBodyChunk(chunkOffset: number, maxChunkSize: number): Buffer | undefined {
    if (chunkOffset < 0) {
      throw new Error('chunkOffset must be greater than or equal to 0')
    }
    if (maxChunkSize <= 0) {
      throw new Error('maxChunkSize must be greater than 0')
    }

    if (!this.body) {
      return undefined
    }

    if (chunkOffset >= this.body.byteLength) {
      return undefined
    }

    const bytesToRead = Math.min(maxChunkSize, this.body.byteLength - chunkOffset)
    const chunk = this.body.slice(chunkOffset, chunkOffset + bytesToRead)
    return chunk
  }

  static createFromResponse(response: EdgeFunctionResponse) {
    const clone_response = new EdgeFunctionResponse({
      statusCode: response.statusCode,
      statusMessage: response.statusMessage,
      body: '',
      headers: response.headers.headers(),
    })
    return clone_response
  }
}
