import Mod from './Mod'
import Phase from '../Phase'
import { Features } from '../../types'

/**
 * Emulation of Sailfish's mod_proxy_core follow_redirects
 */
export default class ModProxyFeatures extends Mod {
  async execute(phase: Phase) {
    this.context.forEachMatchingRule(rule => this.applyFeatures(rule, phase))
  }

  private applyFeatures(features: Features, _phase: Phase): void {
    this.context.ignoreUnsatisfiableRanges =
      features?.caching?.ignore_unsatisfiable_ranges ?? this.context.ignoreUnsatisfiableRanges
  }

  toString() {
    return 'ModProxyFeatures'
  }
}
