import nonWebpackRequire from './nonWebpackRequire'

/**
 * Dynamically requires the version of a package listed in @edgio/core's depedencies
 * This allows us to ensure we are targeting the version of a package, such as globby, that is listed
 * in @edgio/core's package.json, as opposed to whatever version may or may not be included in the user's
 * app, which may not be compatible.
 */
export default function requireInternal(packagePath: string): any {
  try {
    const resolved = require.resolve(packagePath, {
      paths: [require.resolve('@edgio/core')],
    })

    return nonWebpackRequire(resolved)
  } catch (e) {
    return nonWebpackRequire(packagePath)
  }
}
