import EdgeFunctionsBuilder from './EdgeFunctionsBuilder'
import { readFileSync, writeFileSync } from 'fs'
import { Router } from '../../router'
import { isAbsolute, join, dirname, relative } from 'path'
import { build, BuildOptions, transform, TransformOptions } from 'esbuild'
import { isCloud, isLocal, isProductionBuild } from '../../environment'
import { compileEdgeFunction } from '../../runtime/edge-functions/EdgeFunctionCompiler'
import EdgeFunctionsWorkerManager from '../../runtime/edge-functions/EdgeFunctionsWorkerManager'
import { getIndexPath, getBundlePath, getQuickJSBytecodePath } from './paths'
import { containsEdgeFunctionFeature } from '../../utils/edgeFunctionUtils'
import { getJsInternalPath } from '../paths'

/**
 * The edge functions builder for NodeJS environment.
 * This builder uses esbuild to bundle the index file, so it allows to use imports in edge functions.
 * This builder builds the index file 'edgeFunctionsIndexCodeTemplate' with sdk, edge functions and bundle it.
 * The index file is then compiled to QuickJS bytecode.
 */
export default class EdgeFunctionsNodeBuilder extends EdgeFunctionsBuilder {
  protected baseDir: string

  /**
   * A map of edge function paths to source code to upload for display in console.
   */
  public sources: { [key: string]: string } = {}

  /**
   * @param Router The Router
   * @param baseDir The base of the project directory
   */
  constructor(router: Router, baseDir: string) {
    super(router)
    this.baseDir = isAbsolute(baseDir) ? baseDir : join(process.cwd(), dirname(baseDir))
  }

  /**
   * Returns the code with edge function import as string
   * which can later be inserted into edge functions map.
   * @returns
   */
  protected getFunctionCode(srcPath: string, isInitScript: boolean) {
    // Accumulate the source code for deploys so it can be displayed in the console.
    const funcPath = join(this.baseDir, srcPath)
    this.sources[srcPath] = readFileSync(funcPath, 'utf8')

    // Here we use a require('module/path') call instead of import * from 'module/path' to ensure that side effects
    // are only executed if the function is used. This may help reduce unexpected behavior
    // from side-effects caused by poorly written code.
    return `const ${this.functionDefaultExportName} = require(${JSON.stringify(
      join(this.baseDir, srcPath)
    )}).${isInitScript ? 'handleHttpInit' : 'handleHttpRequest'};`
  }

  /**
   * Returns the code of prebuilt the index code template from the file system.
   * @returns
   */
  protected async getIndexCodeTemplate(): Promise<string> {
    return readFileSync(join(__dirname, this.indexCodeTemplateFilename), 'utf8')
  }

  /**
   * Bundles the generated index code into a single file bundle
   * and returns the bundled code
   * @returns
   */
  public async bundleIndexCode(
    indexFilePath = getIndexPath(),
    writeLocalFile: boolean
  ): Promise<string> {
    const indexCode = await this.getIndexCode()
    writeFileSync(indexFilePath, indexCode)
    // We bundle the index file to a single file bundle
    // so the all imports in edge functions are resolved and added to bundle.
    const { code: bundledIndexCode } = await this.bundle(indexFilePath, writeLocalFile)
    return bundledIndexCode
  }

  /**
   * Builds all edge functions referenced by the specified rules and compiles them.
   * Returns a Buffer with the compiled bytecode for the edge functions index file
   * or undefined if there are no edge functions.
   * @param options Additional options with custom paths
   * @returns
   */
  public async build(
    options: {
      indexFilePath?: string
      bytecodePath?: string
    } = {}
  ): Promise<Buffer | undefined> {
    // Check if there are any edge function features at all.
    if (!containsEdgeFunctionFeature(this.router.rules)) return undefined

    options.indexFilePath = options.indexFilePath || getIndexPath()
    options.bytecodePath = options.bytecodePath || getQuickJSBytecodePath()

    let bundledIndexCode: string
    try {
      bundledIndexCode = await this.bundleIndexCode(options.indexFilePath, isLocal())
    } catch (e: any) {
      // If there is a bundling error, the bundler will display the error for us. So just return
      // undefined here. This prevents the CLI from exiting when edge functions have a bundling error.
      return undefined
    }
    // Use relative path when compiling so stack traces do not have absolute paths which
    // may otherwise leak information about the developer's machine.
    const relativePath = relative(process.cwd(), getBundlePath())
    const bytecode = await compileEdgeFunction(bundledIndexCode, relativePath)
    writeFileSync(options.bytecodePath, bytecode)

    // On successful build, reset the edge function memory so it will use cold start.
    EdgeFunctionsWorkerManager.clearWasmInitialMemory()
    return bytecode
  }

  /**
   * Bundles the edge functions index at the specified path into a single file bundle.
   * @param indexPath
   * @returns
   */
  async bundle(indexPath: string, writeLocalFile: boolean): Promise<any> {
    const esBuildOptions: BuildOptions = {
      entryPoints: [indexPath],
      bundle: true,
      minify: isProductionBuild(),
      // format: cjs, with mainFields: [ module, main] gives the same output as
      // platform: node, but without allowing NodeJs builtins like `url` to be imported.
      format: 'cjs',
      platform: 'neutral',
      target: 'es2020', // NODE_VERSION
      mainFields: ['module', 'main'],
      sourcemap: false,
      sourceRoot: isCloud() ? getJsInternalPath() : '',
      write: false, // output to memory instead of writing to disk
    }
    const result = await build(esBuildOptions)
    const { TextDecoder } = require('util')
    const textDecoder = new TextDecoder()

    let code = ''
    for (let out of result.outputFiles || []) {
      code += textDecoder.decode(out.contents)
    }
    if (writeLocalFile) {
      writeFileSync(getBundlePath(), code)
    }
    return { code }
  }

  /**
   * Converts the edge function into cjs
   * @param code
   * @returns cjs
   */
  async convertToCjs(code: string): Promise<string> {
    const esTransformOptions: TransformOptions = {
      minify: false,
      target: 'node16.0', // NODE_VERSION
      format: 'cjs',
    }
    const result = await transform(code, esTransformOptions)
    return result.code
  }
}
