import { existsSync } from 'fs'
import { join } from 'path'
import Config from '../config'
import { isCloud, isProductionBuild } from '../environment'

/**
 * The .edgio build directory
 */
export const EDGIO_DIR = '.edgio'

/**
 * The destination directory with entrypoint for lambda function.
 * This directory contains two directories: internal and app.
 */
export const LAMBDA_DIR = join(EDGIO_DIR, 'lambda')

/**
 * The name of destination directory for our internal JS files such as
 * handler, backends, router etc...
 */
export const JS_INTERNAL_DIR_NAME = 'internal'

/**
 * The relative path to destination directory for our internal JS files such as
 * handler, backends, router etc...
 */
export const JS_INTERNAL_DIR = join(LAMBDA_DIR, JS_INTERNAL_DIR_NAME)

/**
 * The name of destination directory for user's file with JS code
 * with server and its dependencies
 */
export const JS_APP_DIR_NAME = 'app'

/**
 * The relative path to destination directory for user's file with JS code
 * with server and its dependencies
 */
export const JS_APP_DIR = join(LAMBDA_DIR, JS_APP_DIR_NAME)

/**
 * The name of the bundled edge functions index file
 */
export const EDGE_FUNCTIONS_INDEX_FILENAME = '__edge-functions-index__.js'

/**
 * The name of the file containing edge function source code
 */
export const EDGE_FUNCTION_SOURCES_FILENAME = '__edge-function-sources__.json'

/**
 * The name of the bundled edge functions file
 */
export const EDGE_FUNCTIONS_BUNDLE_FILENAME = 'edge-function.js'

/**
 * The path of the bundled edge functions file
 */
export const EDGE_FUNCTIONS_BUNDLE_PATH = join(JS_INTERNAL_DIR, EDGE_FUNCTIONS_BUNDLE_FILENAME)

/**
 * The path to the edge functions index file
 */
export const EDGE_FUNCTIONS_INDEX_PATH = join(JS_INTERNAL_DIR, EDGE_FUNCTIONS_INDEX_FILENAME)

/**
 * The name of the bundled edge functions index compiled QuickJS bytecode file
 */
export const EDGE_FUNCTIONS_QUICKJS_BYTECODE_FILENAME = '__edge-functions__.qbc'

/**
 * The path to the bundled edge functions index compiled QuickJS bytecode file
 */
export const EDGE_FUNCTIONS_QUICKJS_BYTECODE_PATH = join(
  JS_INTERNAL_DIR,
  EDGE_FUNCTIONS_QUICKJS_BYTECODE_FILENAME
)

/**
 * The destination directory for static assets
 */
export const ASSETS_DIR = join(EDGIO_DIR, 's3')
export const PERMANENT_ASSETS_DIR = join(EDGIO_DIR, 's3-permanent')

/**
 * The directory into which source code is copied for debugging purposes
 */
export const SOURCES_DIR = join(EDGIO_DIR, 'src')

/**
 * The directory for temporary files during the build process.
 * This directory is cleaned after the build process is complete.
 */
export const TMP_DIR = join(EDGIO_DIR, 'tmp')

/**
 * The name of the compiled routes file
 */
export const ROUTES_FILE_NAME = 'routes.cjs'

/**
 * The name of the compiled edgio config file
 */
export const CONFIG_FILE_NAME = 'edgio.config.cjs'

/**
 * The static asset manifest file name
 * @private
 */
export const STATIC_ASSET_MANIFEST_FILE_NAME = 'static-asset-manifest.json'

/**
 * The static asset expiration file name
 * @private
 */
export const STATIC_ASSET_EXPIRATION_FILE_NAME = 'static-asset-expiration.json'

/**
 * Returns the destination path within the lambda dir for the specified backend
 * @param backend A backend name
 * @return a file path
 */
export function pathForBackend(backend: string) {
  return join('__backends__', `${backend}.cjs`)
}

/**
 * Returns the path to the routes.js file with the router.
 * @param config The config from edgio.config.js
 * @param cwd The current working directory
 * @returns
 */
export function getRouterPath(config?: Config) {
  // When app is running in production mode,
  // the built router file from the internal folder is returned.
  // Otherwise, the router file from the project root folder is returned.
  const folder = isProductionBuild() && isCloud() ? getJsInternalPath() : process.cwd()

  const result = (
    config?.routes
      ? [join(process.cwd(), config.routes)]
      : [join(folder, 'routes.js'), join(folder, 'routes.ts'), join(folder, 'routes.cjs')]
  ).find(existsSync)

  if (result == null) {
    const srcFile = config?.routes || `routes.js, routes.ts, or routes.cjs`

    throw new Error(
      `Edgio routes file not found. Please create ${srcFile} in the root directory of your project.`
    )
  }
  return result
}

/**
 * Gets the path to edgio.config.js file
 * @returns
 */
export function getConfigPath() {
  // When app is running in production mode,
  // the built config file from the internal folder is returned.
  // Otherwise, the config file from the project root folder is returned.
  const folder = isProductionBuild() && isCloud() ? getJsInternalPath() : process.cwd()

  const result = [
    join(folder, 'edgio.config.js'),
    join(folder, 'edgio.config.ts'),
    join(folder, 'edgio.config.cjs'),
  ].find(existsSync)

  if (result == null) {
    throw new Error(
      'Edgio config file not found. Please create edgio.config.js or edgio.config.cjs in the root directory of your project.'
    )
  }

  return result
}

/**
 * Gets the path to static-asset-manifest.json file
 * @returns
 */
export function getStaticAssetManifestPath() {
  const filePath = join(getJsInternalPath(), STATIC_ASSET_MANIFEST_FILE_NAME)

  if (!existsSync(filePath)) {
    throw new Error(`The '${filePath}' file was not found. Please try to re-run 'edgio build'.`)
  }

  return filePath
}

/**
 * Returns the correct path to the '.edgio/lambda/app' folder
 * with user's JS code in both dev and prod modes.
 * @param cwd
 * @returns
 */
export function getJsAppPath(cwd = process.cwd()): string {
  const pathInDev = join(cwd, JS_APP_DIR)
  const pathInProduction = join(cwd, '..', JS_APP_DIR_NAME)
  const path = isProductionBuild() && isCloud() ? pathInProduction : pathInDev
  return existsSync(path) ? path : process.cwd()
}

/**
 * Returns the path to the '.edgio/lambda/internal' folder
 * with our edgio's JS files such as routes.cjs or edgio.config.cjs.
 * @param cwd
 * @returns
 */
export function getJsInternalPath(cwd = process.cwd()): string {
  const pathInDev = join(cwd, JS_INTERNAL_DIR)
  const pathInProduction = join(cwd, '..', JS_INTERNAL_DIR_NAME)
  const path = isProductionBuild() && isCloud() ? pathInProduction : pathInDev
  return existsSync(path) ? path : process.cwd()
}
