/**
 * `isEmpty` function instead of importing lodash.
 *
 * Note that this function is not entirely equivalent to lodash's but it uses far less space in the bundle.
 */
export default function isEmpty(value: any) {
  if (!value) {
    return true
  }

  if (typeof value.length === 'number') {
    return !value.length
  }

  if (typeof value === 'object' && Object.keys(value).length) {
    return false
  }

  return true
}
