import { TimeInterval } from '../../types'

/**
 * Converts to from number or string to TimeInterval type, this is mostly
 * done when we do conversion from EdgeJs fluent API calls to Sailfish definition
 * @param value
 * @returns
 */
export const toTimeInterval = (value: number | string): TimeInterval => {
  if (typeof value === 'string') {
    return value as TimeInterval
  }

  return `${value}s` as TimeInterval
}
