import { join, resolve } from 'path'
import Config, { getConfig } from '../config'
import Router from '../router/Router'
import Cache, { isCacheEnabled } from './Cache'
import PropertyContext from './PropertyContext'
import RequestContext from './RequestContext'
import { isProductionBuild } from '../environment'
import { ROUTES_FILE_NAME } from '../deploy/paths'
import createEdgeConfig from './createEdgeConfig'
import { getEdgeFunctionBytecode } from '../deploy/edge-functions'
import { Origins } from '../types'
import { EDGIO_CLOUD_FUNCTIONS_HINT_HEADER } from '../constants'
import logger from '../log'

const REDIRECT_STATUS_CODES = new Set<number | undefined>([301, 302, 303, 307, 308])
import LambdaResponse from './LambdaResponse'
import LambdaRequest from './LambdaRequest'

export default class RequestHandler {
  private cache?: Cache
  private router?: Router
  private propertyContext?: PropertyContext
  private config: Config = {} as Config

  constructor(public basedir: string, public injectOrigins: Origins[] = []) {
    if (isCacheEnabled()) {
      this.cache = new Cache()
    }

    if (isProductionBuild()) {
      // Load the config and router immediately if we are running a production build
      // We don't need to wait for a build like we do in dev mode.
      this.reload()
    }
  }

  reload(router?: Router, propertyContext?: PropertyContext) {
    this.config = this.withOrigins(getConfig(!isProductionBuild()))
    const routerPath = resolve(join(this.basedir, ROUTES_FILE_NAME))
    this.router = router ?? Router.load(routerPath)
    this.propertyContext =
      propertyContext ??
      new PropertyContext({
        ...this.config,
        edge_functions: {
          quickjs_bytecode_base64: getEdgeFunctionBytecode(this.router.rules)?.toString('base64'),
        },
      })
    delete require.cache[routerPath]

    this.cache?.clear()

    return this.router
  }

  withOrigins(config: Config) {
    return {
      ...config,
      origins: [...(config.origins ?? []), ...this.injectOrigins],
    } as Config
  }

  async handle(request: LambdaRequest, response: LambdaResponse): Promise<void> {
    try {
      if (this.router && this.propertyContext) {
        const serverlessHint = request.getHeader(EDGIO_CLOUD_FUNCTIONS_HINT_HEADER)
        const context = this.createRequestContext(request, response)

        logger.debug(`[RequestHandler] ${request.method} ${request.url}`)

        if (serverlessHint) {
          // TODO: call when streaming from lambda is possible
          // response.setDirectStream(true)
          await context.executeServerless()
        } else {
          await context.executeSimulator()
        }

        logger.debug(
          `[RequestHandler] response status: ${response.statusCode} ${response.statusMessage || ''}`
        )

        if (context.followRedirects && REDIRECT_STATUS_CODES.has(response.statusCode)) {
          context.followRedirects = false
          await this.handleRedirects(request, response)

          // Sailfish only allows a single redirect. If the user attempts a second, it will throw a 500 error.
          if (context.followRedirects && REDIRECT_STATUS_CODES.has(response.statusCode)) {
            throw new Error('Too many internal redirects')
          }
        }
      } else {
        throw new Error('Server not yet initialized.')
      }
    } catch (e: any) {
      // we clear any current chunks (could be there if error happens during proxy, computes, and other stuff that doesnt stream)
      response.clear()
      response.statusCode = 500
      response.statusMessage = 'Internal Server Error'
      response.write(JSON.stringify({ error: e.stack }))
      response.end()
      response.stream()
      await response.waitForFlush()
    }
  }

  private createRequestContext(request: LambdaRequest, response: LambdaResponse) {
    return new RequestContext({
      request,
      response,
      propertyContext: this.propertyContext!,
      rules: this.router!.rules,
      edgeFunctions: {
        quickjs_bytecode_base64: getEdgeFunctionBytecode(this.router!.rules)?.toString('base64'),
      },
      cache: this.cache,
      functions: this.router!.functions,
      interpolationValues: this.config.interpolationValues,
    })
  }

  /**
   * Simulates the Sailfish front end behavior of following redirects.
   * This is needed to implement retry.
   * @param request
   * @param response
   */
  private async handleRedirects(request: LambdaRequest, response: LambdaResponse) {
    const location = response.getHeader('location') as string
    const url = new URL(location)

    logger.debug(`[RequestHandler] Following redirect to ${location}`)

    // Clone the request and use the redirect location as the URL
    const clone = request.cloneOriginal()
    clone.url = url.pathname + url.search
    clone.path = url.pathname

    // Undo any changes to the response made by the original request
    response.clear()
    response.statusCode = 200
    response.statusMessage = 'OK'
    response.body = ''
    Object.keys(response.getHeaders()).forEach(header => response.removeHeader(header))

    await this.handle(clone, response)
  }

  createEdgeConfig() {
    if (this.router && this.propertyContext) {
      return createEdgeConfig(this.propertyContext, this.router)
    } else {
      throw new Error('Not yet initialized.')
    }
  }

  createPreloadConfig() {
    if (!this.router) {
      throw new Error('Not yet initialized.')
    }
    return this.router.preloadRequests.getPreloadConfig()
  }
}
