/* istanbul ignore file */

// Initialize the LambdaInstance object once per lambda lifetime to capture the metrics.
// This is done as early as possible so that we can accurately measure
// cold starts. To get statistics on cold starts search for requests
// that have the "worker counter" equal to 1 and then compare "worker time"
// with "worker age".
const { default: LambdaInstance } = require('./lambdaInstance')
const instance = new LambdaInstance()

// Load HTTP request interception as soon as possible so that we can monkey patch the underlying
// Node modules before anything else had a chance to take a reference on the original
// classes and functions.
const { interceptRequests } = require('./httpRequestInterceptor')

// This handler is a thin wrapper around the actual inner handler which concerns itself
// with lambda jobs, metrics, error handling and so on. The responsibility of this handler
// is to load the HTTP request interception module before anything else is loaded.
export async function handler(event: any) {
  interceptRequests(event, {
    enableHttpRequestLogging: process.env.EDGIO_HTTP_REQUEST_LOGGING === '1',
    lambdaInstance: instance,
  })

  // Late load the inner handler so that it never interferes with the HTTP request interception module.
  return (async () => await require('./innerHandler').default(event, instance))()
}
