import RouteCriteria, {
  OptionalCriteriaValue,
  isNegation,
  InOperatorCriteriaValue,
} from './RouteCriteria'
import { match } from 'path-to-regexp'
import { isParamMatcher, paramMatcherToParam } from '../utils/universalRouteUtils'
import Request from '../runtime/Request'
import { ExactPath } from '../router/exact'

export default class ParamsExtractor {
  protected routeCriteria: RouteCriteria

  constructor(routeCriteria: RouteCriteria) {
    this.routeCriteria = {}
    this.updateRouteCriteria(routeCriteria)
  }

  updateRouteCriteria(routeCriteria: RouteCriteria) {
    this.routeCriteria = {
      path: routeCriteria?.path,
      query: routeCriteria?.query,
    }
  }

  /**
   * Returns the object with extracted params from request path and query matched by latest routeCriteria
   * @returns
   */
  extract(request: Request) {
    return {
      ...(this.routeCriteria?.path && !isNegation(this.routeCriteria.path)
        ? ParamsExtractor.getMatchedPathParams(this.routeCriteria.path, request.path)
        : {}),
      ...(this.routeCriteria?.query
        ? ParamsExtractor.getMatchedQueryParams(this.routeCriteria.query, request.query)
        : {}),
    }
  }

  /**
   * Returns params from request path matched by route criteria path
   * @param criteriaPath
   * @param requestPath
   * @returns
   */
  static getMatchedPathParams(
    criteriaPath: string | RegExp | ExactPath | InOperatorCriteriaValue,
    requestPath: string
  ): Record<string, string | string[]> | undefined {
    // When criteriaPath is a RegExp, we extract matched groups from requestPath
    // and expose them in params object under "$" key.
    // Example: $1 => req.params.$[1]
    if (criteriaPath instanceof RegExp)
      return {
        $: criteriaPath.exec(requestPath) || [],
      }

    if (Array.isArray(criteriaPath)) return undefined

    const pathMatcher = match(criteriaPath.toString(), { decode: decodeURIComponent })
    const result = pathMatcher(requestPath)

    return result ? <{ [key: string]: string | string[] }>result.params : undefined
  }

  /**
   * Returns params from request query matched by route criteria query
   * @param criteriaQuery object of routeCriteria queries
   * @param requestQuery object of parsed queries
   * @returns
   */
  static getMatchedQueryParams(
    criteriaQuery: { [name: string]: OptionalCriteriaValue | InOperatorCriteriaValue },
    requestQuery: { [key: string]: string | string[] | undefined }
  ): Record<string, string> | undefined {
    let extractedParams = {}
    if (typeof requestQuery !== 'object') return extractedParams

    for (const criteriaQueryKey in criteriaQuery) {
      const criteriaQueryValue = criteriaQuery[criteriaQueryKey]

      if (typeof criteriaQueryValue !== 'string' || !isParamMatcher(criteriaQueryValue)) continue
      const paramName = paramMatcherToParam(criteriaQueryValue)
      extractedParams = {
        ...extractedParams,
        [paramName]: requestQuery[paramName],
      }
    }

    return extractedParams
  }
}
