import { ConditionCriteria } from '../RouteCriteria'
import { Features, Rules } from '../../types'
import { createMatchers } from '../createMatchers'

// We allow rules and routers in any order
export type FeaturesOrRouter = Features | { rules: Rules[] } | Rules[]

export const toRule = (
  criteria: ConditionCriteria,
  ...featuresOrRouters: FeaturesOrRouter[]
): Rules => {
  const matchers = createMatchers(criteria)
  const then = toThen(featuresOrRouters)

  // when there are no matchers for rule, we must return only feature as rule, as empty criteria is invalid and fails deployment
  if (!matchers) {
    return then as Features
  }

  const rule: Rules = {
    if: [matchers, then as Features],
  }

  return rule
}

export const toThen = (featuresOrRouters: FeaturesOrRouter[] = []): Features | Rules[] => {
  const outputRules: any[] = []

  featuresOrRouters.forEach((featureOrRouter: any) => {
    const value: any = featureOrRouter
    value.rules
      ? outputRules.push(...value.rules)
      : Array.isArray(value)
      ? outputRules.push(...value)
      : outputRules.push(value)
  })

  if (outputRules[0]?.if) {
    // Rule array must ALWAYS start with feature - console parses that feature as a root feature of the rule.
    // Otherwise, they wouldnt be able to discern between whats root feature, and whats nested feature - it is what it is.
    return [{}, ...outputRules]
  } else if (outputRules.length === 1) {
    // If rule array has only 1 feature, we dont use the array -> feature will therefore not be a nested rule on console side.
    return outputRules[0]
  }
  return outputRules
}

export const isRule = (value: any) => !!value.if
