import Mod from './Mod'
import Phase from '../Phase'
import { BROTLI_ENCODING, DEFLATE_ENCODING, GZIP_ENCODING, HTTP_HEADERS } from '../../constants'
import zlib from 'zlib'
import { parse as parseContentType } from 'content-type'

/**
 * Those content types that will be gzipped are generated from the lists in:
 * our Edgio v1 production Nginx + Edgio v1 production Varnish + default CDN gzip policy
 *
 * Set is not always optimal but in the benchmarks came out on top the most times.
 * It is also the easiest to read and check for correctness.
 *
 * NOTE: this is not related to sailfish, but rather previous implementation of encoding
 * we were returning from app. Therefore, we use this only in production / when not running simulator.
 */
const COMPRESSIBLE_TYPES = new Set([
  'application/atom_xml',
  'application/javascript',
  'application/json',
  'application/rss+xml',
  'application/vnd.ms-fontobject',
  'application/x-font-opentype',
  'application/x-font-ttf',
  'application/x-javascript',
  'application/xhtml+xml',
  'application/xml',
  'application/xml+rss',
  'font/eot',
  'font/opentype',
  'font/otf',
  'image/svg+xml',
  'image/x-icon',
  'text/css',
  'text/html',
  'text/javascript',
  'text/js',
  'text/plain',
])

/**
 * Emulation of Sailfish's mod_transcode
 */
export default class ModTranscode extends Mod {
  async execute(phase: Phase) {
    // we return early if response is not encodable
    if (!this.isEncodable()) return

    if (
      (this.context.useCacheFile && phase === Phase.HandleDocRoot) ||
      phase === Phase.StreamResponse ||
      phase === Phase.HandleResponseHeader
    ) {
      const request = this.context.getRequest()
      const response = this.context.getResponse()

      const contentEncoding: string = response.getHeader(HTTP_HEADERS.contentEncoding) as string
      const acceptEncoding: string = request.getHeader(HTTP_HEADERS.acceptEncoding) as string

      const BROTLI_ENCODING_REGEX = /\bbr\b/
      const GZIP_ENCODING_REGEX = /\bgzip\b/
      const DEFLATE_ENCODING_REGEX = /\deflate\b/
      const downstreamIsGzipCompatible = GZIP_ENCODING_REGEX.test(acceptEncoding)
      const downstreamIsBrotliCompatible = BROTLI_ENCODING_REGEX.test(acceptEncoding)
      const downstreamIsDeflateCompatible = DEFLATE_ENCODING_REGEX.test(acceptEncoding)

      if (downstreamIsGzipCompatible && contentEncoding === GZIP_ENCODING) {
        return
      }
      if (downstreamIsBrotliCompatible && contentEncoding === BROTLI_ENCODING) {
        return
      }
      if (downstreamIsDeflateCompatible && contentEncoding === DEFLATE_ENCODING) {
        return
      }

      // we delete content-length, as we cant know it before reencoding
      response.removeHeader(HTTP_HEADERS.contentLength)
      response.setHeader(HTTP_HEADERS.transferEncoding, 'chunked')

      // left here for testing purposes - undefined == default chunk size
      const chunkSize = undefined

      switch (contentEncoding) {
        case GZIP_ENCODING:
          response.setDecoder(zlib.createGunzip({ chunkSize }))
          break
        case BROTLI_ENCODING:
          response.setDecoder(
            zlib.createBrotliDecompress({
              chunkSize,
            })
          )
          break
        case DEFLATE_ENCODING:
          response.setDecoder(zlib.createInflate({ chunkSize }))
          break
      }

      let contentType: string = response.getHeader(HTTP_HEADERS.contentType) as string
      if (Array.isArray(contentType)) {
        contentType = contentType[0]
      }
      const parsedContentType = contentType && parseContentType(contentType).type.toLowerCase()

      if (
        this.context.executingSimulator ||
        (parsedContentType && COMPRESSIBLE_TYPES.has(parsedContentType))
      ) {
        switch (acceptEncoding) {
          case GZIP_ENCODING:
            response.setEncoder(zlib.createGzip({ chunkSize }))
            response.setHeader(HTTP_HEADERS.contentEncoding, GZIP_ENCODING)
            break
          case BROTLI_ENCODING:
            response.setEncoder(
              zlib.createBrotliCompress({
                chunkSize,
                params: {
                  // TODO: probably should be settable through some header? is there some spec for it? default is 11, which is 37.29Kbps, 7 is 2.7Mbps etc.
                  // https://quixdb.github.io/squash-benchmark/#results-table
                  [zlib.constants.BROTLI_PARAM_QUALITY]: 5,
                },
              })
            )
            response.setHeader(HTTP_HEADERS.contentEncoding, BROTLI_ENCODING)
            break
          case DEFLATE_ENCODING:
            response.setEncoder(zlib.createDeflate({ chunkSize }))
            response.setHeader(HTTP_HEADERS.contentEncoding, DEFLATE_ENCODING)
        }
      } else {
        response.removeHeader(HTTP_HEADERS.contentEncoding)
      }
      response.initDownstream()
    }
  }

  // following response codes were copied straight from sailfish -> some status codes arent encoded, and sailfish returns early
  private isEncodable() {
    const responseCode = this.context.getResponse().statusCode
    return ![100, 101, 204, 205, 206, 302, 304, 307].find(
      invalidCode => invalidCode === responseCode
    )
  }

  toString() {
    return 'ModTranscode'
  }
}
