import log from '../log'
import Request from './Request'
import Response from './Response'
import { OriginBackends, Origins } from '../types'
import Backend, { BackendFetchOptions } from './Backend'
import { getRandomElement } from './random'

export default class Origin {
  private config: Origins

  constructor(config: Origins) {
    this.config = config
  }

  async fetch(
    request: Request,
    response: Response,
    options: BackendFetchOptions = {}
  ): Promise<void> {
    const hostConfig = this.selectHost()

    const host = Backend.create(hostConfig, this.config)
    return host.fetch(request, response, options)
  }

  /**
   * Selects a backend based on the "balancer" property
   */
  private selectHost(): OriginBackends {
    const { balancer } = this.config

    if ('static_weighted' === balancer) {
      return this.config.hosts![0]
    } else if ('round_robin' === balancer) {
      // TODO instead of calling a random one, iterate through indexes for each call
      return getRandomElement(this.config.hosts!)
    } else {
      // TODO implement other load balancing strategies, static_weighted
      if (balancer) {
        log.warn(
          `balancer type "${balancer}" is not supported during local development, falling back to "static".`
        )
      }
      return this.config.hosts![0]
    }
  }
}
