import WasmMemory from './WasmMemory'

// In WASM, this is the size of the wasi_iovec_t struct.
export const SIZEOF_wasi_iovec_t = 8

// Maximum size of read from the guest memory. Use for all string except the body functions.
const MAX_GUEST_STRING_BYTES = 10 * 1024

// Connascence of meaning and values with the wasm_status from mod_wasm.
export enum wasm_status {
  ok = 0, // hostcall finished successfully
  system_error = 1, // hostcall encountered a system error - this should result in a Wasm trap and is never communicated directly to the guest status
  not_found = 2, // hostcall could not find the specified resource (header did not exist for example)
  insufficient_buffer = 3, // hostcall requires a large memory region to copy data back to the host
  invalid_args_count = 4, // the number of arguments that the hostcall received does not match the number of arguments that the hostcall is expecting
  invalid_argument = 5, // at least one argument to the hostcall was not valid
  invalid_operation = 6, // invoking the particular hostcall at the current state of processing is not valid
  invalid_id = 7, // the resource ID provided to the hostcall is not valid (a more specialized version of invalid_argument)
  limit_exceeded = 8, // a non-terminating system limit was exceeded
  client_abandoned = 9, // the client has abandoned the request
}

export class WasmError {
  public status: wasm_status = wasm_status.ok
  public message?: string

  static ok(): WasmError {
    return new WasmError(wasm_status.ok, undefined)
  }

  constructor(status: wasm_status, message?: string) {
    this.status = status
    this.message = message
  }
}

/// Base class for all Wasm programs providing common functionality.
export default class WasmProgram {
  private instance: WebAssembly.Instance | undefined
  protected guestErrorMessage?: string
  protected lastHostErrorMessage: string | undefined

  protected setInstance(instance: WebAssembly.Instance) {
    this.instance = instance
  }

  /// Returns the DataView with the memory of the WASM function.
  protected getGuestMemory(): WasmMemory {
    // @ts-ignore
    return new WasmMemory(new DataView(this.instance!.exports.memory.buffer))
  }

  protected getStringFromGuestMemory(
    memory: WasmMemory,
    offset: number,
    len: number
  ): string | WasmError {
    if (len > MAX_GUEST_STRING_BYTES) {
      return new WasmError(
        wasm_status.invalid_argument,
        `(length ${len} > ${MAX_GUEST_STRING_BYTES} bytes) Argument is too long`
      )
    }
    return this.getStringFromGuestMemoryNoLimit(memory, offset, len)
  }

  protected getStringFromGuestMemoryTruncate(
    memory: WasmMemory,
    offset: number,
    len: number
  ): string {
    len = Math.min(len, MAX_GUEST_STRING_BYTES)
    return this.getStringFromGuestMemoryNoLimit(memory, offset, len)
  }

  protected getStringFromGuestMemoryNoLimit(
    memory: WasmMemory,
    offset: number,
    len: number
  ): string {
    return new TextDecoder().decode(memory.slice(offset, offset + len))
  }

  protected setStringInGuestMemory(
    data_offset: number,
    data_len_offset: number,
    value: string
  ): wasm_status {
    const data = new TextEncoder().encode(value)
    return this.copyDataToGuestMemory(data, data_offset, data_len_offset)
  }

  // Used to restore the guest memory to the state immediately after initialization.
  protected restoreGuestMemory(wasmInitialMemory: Buffer) {
    this.getGuestMemory().setBuffer(wasmInitialMemory)
  }

  // Copies the given data into the WASM memory at the given offset.
  // Connascence of algorithm with the `copy_to_guest` function in the mod_wasm.
  // See hostcalls.h
  protected copyDataToGuestMemory(
    data: Uint8Array,
    data_offset: number,
    len_offset: number
  ): wasm_status {
    const memory = this.getGuestMemory()

    // Read the length of the data buffer.
    const len = memory.getUint32(len_offset)
    // Always write the size of the data into the given len pointer so that guest
    // can know how much data there is and grow the buffer if needed.
    memory.setUint32(len_offset, data.byteLength)

    if (data.byteLength === 0) {
      return wasm_status.ok
    }
    if (data.byteLength > len) {
      // Signal that the guest needs to grow the buffer.
      return wasm_status.insufficient_buffer
    }

    // Copy the data into the memory.
    // TODO: https://jira.atlas.llnw.com/browse/EAPPS-16
    // Optimize copying of the memory. See details in the JIRA ticket.
    for (let i = 0; i < data.byteLength; i++) {
      memory.setUint8(data_offset + i, data[i])
    }
    return wasm_status.ok
  }
}
