import { Features } from '../../types'
import Mod from './Mod'
import Phase from '../Phase'
import EarlyReturn from '../EarlyReturn'
import { STATUS_CODES } from 'http'

/**
 * Emulation of Sailfish's mod_access
 */
export default class ModAccess extends Mod {
  async execute(phase: Phase) {
    this.context.forEachMatchingRule(rule => this.applyFeatures(rule, phase))
  }

  private applyFeatures(features: Features, _phase: Phase): void {
    if (features.access?.deny_access) {
      const response = this.context.getResponse()
      response.statusCode = 403
      response.statusMessage = STATUS_CODES[403]
      throw new EarlyReturn('deny_access')
    }
  }

  toString() {
    return 'ModAccess'
  }
}
