/**
 * Converts seconds to the most sensible unit abbreviation - the largest unit that overflows rounded down
 * https://docs.edgecast.com/cdn/Content/Knowledge_Base/X_EC_Debug.htm#TimeUnitAbbrev
 */
export const toTimeUnitAbbrev = (seconds: number) => {
  const hours = 60 * 60
  const days = hours * 24
  const months = days * 30.437
  const years = months * 12

  if (seconds >= years) {
    return Math.floor(seconds / years) + 'y'
  } else if (seconds >= months) {
    return Math.floor(seconds / months) + 'm'
  } else if (seconds >= days) {
    return Math.floor(seconds / days) + 'd'
  } else if (seconds >= hours) {
    return Math.floor(seconds / hours) + 'h'
  }
  return seconds + 's'
}
