import { Boolean, Conditionals, Features, Matches, Rules } from '../../types'
import {
  AND,
  EQUALS,
  EQUALS_EXPRESS,
  GREATER_THAN,
  GREATER_THAN_OR_EQUALS,
  IN,
  LESS_THAN,
  LESS_THAN_OR_EQUALS,
  MATCHES,
  NOT_EQUALS,
  NOT_EQUALS_EXPRESS,
  NOT_IN,
  NOT_MATCHES,
  OR,
} from './constants'

/**
 * Config object with callbacks for RulesVisitor
 */
export interface RulesVisitorCallbacks {
  onRuleVisit?: (rule: Rules, ruleIndex: number) => void
  onMatchesVisit?: (matches: Matches, ruleIndex: number) => void
  onFeaturesVisit?: (features: Features, ruleIndex: number) => void
  onConditionalsVisit?: (conditionals: Conditionals, ruleIndex: number) => void
  onBooleanVisit?: (boolean: Boolean, ruleIndex: number) => void
  onConditionVisit?: (condition: any[], operator: string, ruleIndex: number) => void
}

export class RulesVisitorStop extends Error {}

/**
 * Visitor for Rules which allows to visit each node of the Rules structure
 * and call callbacks for each node type.
 */
export default class RulesVisitor {
  protected callbacks: RulesVisitorCallbacks

  constructor(callbacks: RulesVisitorCallbacks = {}) {
    this.callbacks = callbacks
  }

  /**
   * Sets callback which is triggered when Rule node is visited.
   * @param callback
   */
  public onRuleVisit(callback: (rule: Rules, ruleIndex: number) => void) {
    this.callbacks.onRuleVisit = callback
  }

  /**
   * Sets callback which is triggered when Matches node is visited.
   * @param callback
   */
  public onMatchesVisit(callback: (matches: Matches, ruleIndex: number) => void) {
    this.callbacks.onMatchesVisit = callback
  }

  /**
   * Sets callback which is triggered when Features node is visited.
   * @param callback
   */
  public onFeaturesVisit(callback: (features: Features, ruleIndex: number) => void) {
    this.callbacks.onFeaturesVisit = callback
  }

  /**
   * Sets callback which is triggered when Conditionals node is visited.
   * @param callback
   */
  public onConditionalsVisit(callback: (conditionals: Conditionals, ruleIndex: number) => void) {
    this.callbacks.onConditionalsVisit = callback
  }

  /**
   * Sets callback which is triggered when Boolean node is visited.
   * @param callback
   */
  public onBooleanVisit(callback: (boolean: Boolean, ruleIndex: number) => void) {
    this.callbacks.onBooleanVisit = callback
  }

  /**
   * The visitor visits each node of the Rules structure and calls callbacks for each node type.
   * This is starting point of the visitor.
   * @param rules The array of rules
   * @param parentRuleIndex The index of parent rule if we're visiting nested rules
   */
  public visit(rules: Rules[], parentRuleIndex?: number) {
    try {
      rules.forEach((rule, ruleIndex) => this.visitRule(rule, parentRuleIndex ?? ruleIndex))
    } catch (e: any) {
      if (e instanceof RulesVisitorStop) return
      // Rethrow unknown error
      throw e
    }
  }

  /**
   * This method is called when we visit Rule node.
   * @param rule
   * @param ruleIndex
   */
  protected visitRule(rule: Rules, ruleIndex: number) {
    this.callbacks?.onRuleVisit?.(rule, ruleIndex)

    if (this.isMatches(rule)) {
      this.visitMatches(rule as Matches, ruleIndex)
    } else {
      this.visitFeaturesOrRules(rule, ruleIndex)
    }
  }

  /**
   * This method is called when we visit Matches node.
   * @param matches
   * @param ruleIndex
   */
  protected visitMatches(matches: any, ruleIndex: number) {
    this.callbacks?.onMatchesVisit?.(matches, ruleIndex)

    const ifStatements: Array<Conditionals | Boolean | Features | Rules[]> = matches?.if
    if (!ifStatements) return

    ifStatements.forEach((ifStatement: any) => {
      if (this.isConditionals(ifStatement)) {
        this.visitConditionals(ifStatement, ruleIndex)
      } else if (this.isBoolean(ifStatement)) {
        this.visitBoolean(ifStatement, ruleIndex)
      } else {
        this.visitFeaturesOrRules(ifStatement, ruleIndex)
      }
    })
  }

  /**
   * This method is called when we visit Features or Rules node.
   * @param featuresOrRules
   * @param ruleIndex
   */
  protected visitFeaturesOrRules(featuresOrRules: Features | Rules[], ruleIndex: number) {
    this.isRules(featuresOrRules)
      ? this.visit(featuresOrRules, ruleIndex)
      : this.visitFeatures(featuresOrRules, ruleIndex)
  }

  /**
   * This method is called when we visit Features node.
   * @param features
   * @param ruleIndex
   */
  protected visitFeatures(features: Features, ruleIndex: number) {
    this.callbacks?.onFeaturesVisit?.(features, ruleIndex)
  }

  /**
   * This method is called when we visit Conditionals node.
   * @param conditionals
   * @param ruleIndex
   */
  protected visitConditionals(conditionals: Conditionals, ruleIndex: number) {
    this.callbacks?.onConditionalsVisit?.(conditionals, ruleIndex)

    Object.keys(conditionals).forEach(operator => {
      const condition = conditionals[operator]
      this.visitCondition(condition, operator, ruleIndex)
    })
  }

  /**
   * This method is called when we visit Condition node.
   * @param operator
   * @param condition
   * @param ruleIndex
   */
  protected visitCondition(condition: any[], operator: string, ruleIndex: number) {
    this.callbacks?.onConditionVisit?.(condition, operator, ruleIndex)
  }

  /**
   * This method is called when we visit Boolean node.
   * @param boolean
   * @param ruleIndex
   */
  protected visitBoolean(boolean: Boolean, ruleIndex: number) {
    this.callbacks?.onBooleanVisit?.(boolean, ruleIndex)

    const booleanItemHandler = (item: Conditionals | Boolean) => {
      if (this.isBoolean(item)) return this.visitBoolean(item, ruleIndex)
      this.visitConditionals(item, ruleIndex)
    }
    boolean[AND]?.forEach(booleanItemHandler)
    boolean[OR]?.forEach(booleanItemHandler)
  }

  /**
   * Returns true if passed object is Matches type from Rules
   * @param rule
   */
  protected isMatches(rule: Rules): rule is Matches {
    return Object.keys(rule).includes('if')
  }

  /**
   * Returns true if passed object is Rules[] type from Rules
   * @param rule
   */
  protected isRules(rule: Features | Rules[]): rule is Rules[] {
    return Array.isArray(rule)
  }

  /**
   * Returns true if passed object is Boolean type from Rules
   * @param condition
   */
  protected isBoolean(condition: Boolean | Conditionals | Features): condition is Boolean {
    return [AND, OR].some(key => Object.keys(condition).includes(key))
  }

  /**
   * Returns true if passed object is Conditionals type from Rules
   * @param condition
   */
  protected isConditionals(
    condition: Boolean | Conditionals | Features
  ): condition is Conditionals {
    return [
      EQUALS_EXPRESS,
      EQUALS,
      NOT_EQUALS_EXPRESS,
      NOT_EQUALS,
      MATCHES,
      NOT_MATCHES,
      GREATER_THAN,
      GREATER_THAN_OR_EQUALS,
      LESS_THAN,
      LESS_THAN_OR_EQUALS,
      IN,
      NOT_IN,
    ].some(key => Object.keys(condition).includes(key))
  }

  /**
   * This method can be called from any visit method or callback to immediately stop the visitor.
   */
  public stop() {
    throw new RulesVisitorStop()
  }
}
