/**
 * Returns a random element from the source array
 * @param source
 * @returns
 */
export function getRandomElement<TElement, TArray extends Array<TElement>>(source: TArray) {
  const index = getRandomInt(source.length)
  return source[index]
}

/**
 * Returns a random integer between 0 and {max}
 * @param max
 * @returns
 */
export function getRandomInt(max: number) {
  return Math.floor(Math.random() * max)
}
