import CacheKey from './CacheKey'
import LRU from 'lru-cache'
import RequestContext from './RequestContext'
import Response from './Response'
import cloneDeep from 'lodash.clonedeep'
import { EDGIO_ENV_VARIABLES } from '../constants'
import LambdaResponse from './LambdaResponse'

/**
 * We store the responses in the cache differently from their actual
 * representation because they are different classes in different
 * execution environments.
 */
export interface CachedResponse {
  /**
   * The HTTP status code
   */
  statusCode: number

  /**
   * The HTTP status message
   */
  statusMessage?: string

  /**
   * The response body as a array of chunks - done this way in order to replicate the behaviour
   * of the initial, non-cached response.
   */
  chunks: Buffer[]

  /**
   * The response HTTP headers
   */
  headers: { [key: string]: string | string[] }

  /**
   * The time the response was put in the cache
   */
  cachedAt: number

  /**
   * The time to live of the response
   */
  ttl: number

  /**
   * True if the response should be returned stale and revalidated
   */
  serveStaleUntil?: number
}

export interface CacheLookupResult {
  response: CachedResponse
  revalidate: boolean
}

export interface AddToCacheOptions {
  response: Response
  cacheKey: CacheKey
  context: RequestContext
}

export interface CacheConfig {
  ttl: number
  staleWhileRevalidate?: number
}

/**
 * Must have the EDGIO_LOCAL_CACHE environment variable set.
 */
export function isCacheEnabled() {
  return process.env[EDGIO_ENV_VARIABLES.cache] === 'true'
}

/**
 * Local simulation of Edgio's edge cache
 */
export default class Cache {
  private cache = new LRU<string, CachedResponse>({
    ttl: 1,
    allowStale: true,
    ttlAutopurge: false,
    max: 100000,
  })

  // additional cache serving as a "auto-unlocking" lock, used in order to avoid making unnecessary revalidation requests
  private revalidationCache = new LRU<string, boolean>({
    // time we consider OK for additional request to be made on timeout
    ttl: 500,
    allowStale: false,
    ttlAutopurge: false,
    max: 10000,
  })

  /**
   * Evicts all entries from the cache
   */
  clear() {
    this.cache.clear()
  }

  /**
   * Returns the cached response for the specified key
   * @param key The cache key
   */
  get(key: CacheKey): CacheLookupResult | undefined {
    const keyValue = key.toString()
    const response = this.cache.get(keyValue, { allowStale: true, noDeleteOnStaleGet: true })
    const remainingTtl = this.cache.getRemainingTTL(keyValue)
    const stale = remainingTtl <= 0

    if (!response) {
      // no response found in the cache
      return undefined
    }

    const now = Date.now()
    const { serveStaleUntil } = response

    if (stale && (serveStaleUntil == null || serveStaleUntil < now)) {
      // response is stale and outside of the stale-while-revalidate period
      return undefined
    }

    return {
      response: cloneDeep(response), // always return a clone of the cached response so no one can mutate it
      revalidate: stale,
    }
  }

  /**
   * Stores a response in the cache
   * @param cacheKey
   * @param response
   * @param maxAge
   */
  put(
    cacheKey: CacheKey,
    response: LambdaResponse,
    { ttl, staleWhileRevalidate }: { ttl: number; staleWhileRevalidate?: number }
  ) {
    let serveStaleUntil = undefined

    if (staleWhileRevalidate) {
      serveStaleUntil = new Date().getTime() + ttl * 1000 + staleWhileRevalidate * 1000
    }

    const cached = convertToCachedResponse(response, ttl, serveStaleUntil)

    this.cache.set(cacheKey.toString(), cached, {
      ttl: ttl * 1000,
    })
  }

  isRevalidating(cacheKey: CacheKey): boolean {
    return !!this.revalidationCache.get(cacheKey.toString())
  }

  setRevalidating(cacheKey: CacheKey, value: boolean): void {
    this.revalidationCache.set(cacheKey.toString(), value)
  }

  dump() {
    return this.cache.dump()
  }
}

/**
 * We have to deep clone parts of the
 * @param response HTTP response that we are caching
 */
function convertToCachedResponse(
  response: LambdaResponse,
  ttl: number,
  serveStaleUntil?: number
): CachedResponse {
  return {
    cachedAt: Date.now(),
    ttl,
    statusCode: response.statusCode,
    statusMessage: response.statusMessage,
    headers: JSON.parse(JSON.stringify(response.getHeaders())),
    chunks: response.encodedChunks,
    serveStaleUntil,
    // @ts-ignore
    someObject: { key: 'value' },
  }
}
