import RequestContext from '../RequestContext'
import EdgeFunctionResponse from './EdgeFunctionResponse'
import { Buffer } from 'buffer'
import EdgeFunctionsWorkerManager from './EdgeFunctionsWorkerManager'

/// Creates, runs, offers services to, kills and otherwise manages Edge Functions.
export default class EdgeFunctionsManager {
  static s_instance: EdgeFunctionsManager = new EdgeFunctionsManager()

  static instance(): EdgeFunctionsManager {
    return EdgeFunctionsManager.s_instance
  }

  async runEdgeFunction(
    context: RequestContext,
    path: string,
    sysVars: Map<string, string | Uint8Array>,
    envVars: Map<string, string>,
    usrVars: Map<string, string>,
    httpVars: Map<string, string>,
    rawBody?: Buffer
  ): Promise<EdgeFunctionResponse> {
    // We give the edge functions access to the entire request body if it's not empty.
    const maybeRawBody = rawBody?.byteLength ? rawBody : undefined
    const worker = new EdgeFunctionsWorkerManager(
      context,
      maybeRawBody,
      path,
      sysVars,
      envVars,
      usrVars,
      httpVars
    )
    return worker.runEdgeFunction()
  }
}
