import {
  EDGE_FUNCTIONS_INDEX_FILENAME,
  EDGE_FUNCTIONS_BUNDLE_FILENAME,
  EDGE_FUNCTIONS_QUICKJS_BYTECODE_FILENAME,
  getJsInternalPath,
} from '../paths'
import { join } from 'path'

export function getIndexPath() {
  return join(getJsInternalPath(), EDGE_FUNCTIONS_INDEX_FILENAME)
}

export function getBundlePath() {
  return join(getJsInternalPath(), EDGE_FUNCTIONS_BUNDLE_FILENAME)
}

export function getQuickJSBytecodePath() {
  return join(getJsInternalPath(), EDGE_FUNCTIONS_QUICKJS_BYTECODE_FILENAME)
}
