import Response from './runtime/Response'
import { HTTP_HEADERS } from './constants'
import { get, withContext } from './context'

export const NOT_ENDED_VALUE = 'na'

export class InternalTiming {
  /**
   * The name assigned to the timing in response headers
   */
  readonly name: string

  /**
   * The time the timing started
   */
  startTime?: Date

  /**
   * The number of milliseconds elapsed
   */
  duration: number = 0

  /**
   * A discrete value for the timing, for example "hit" for "ecc"
   */
  discreteValue?: string

  /**
   * Set to true if the timing has been ended
   */
  ended: boolean = false

  /**
   * @param name The name of the timing component that will be reported in the `x-edg-user-t` header
   */
  constructor(name: string, internal: boolean = true) {
    this.name = name
    const timings = internal ? internalTimings() : userTimings()
    timings.push(this)
  }

  /**
   * Starts the timing.
   * @returns A self-reference, suitable for chaining
   */
  start() {
    this.startTime = new Date()
    this.ended = false
    return this
  }

  /**
   * Sets the discrete value for this timing
   * @param discreteValue
   * @returns A self-reference, suitable for chaining
   */
  set(discreteValue: string) {
    this.discreteValue = discreteValue
    return this
  }

  /**
   * Ends the timing.
   * @returns A self-reference, suitable for chaining
   */
  end() {
    if (!this.startTime) {
      console.warn(`Timing ${this.name} not started.`)
      return this
    }

    this.ended = true
    this.duration += new Date().getTime() - this.startTime.getTime()
    return this
  }

  /**
   * Convenience method that is the same as `end`, but meant to indicate
   * that the timing may be resumed later
   */
  pause() {
    return this.end()
  }

  /**
   * Convenience method that is the same as `start`, but meant to indicate
   * that the timing has been paused.
   */
  resume() {
    return this.start()
  }

  /**
   * Gets the value of the timing for response headers. Will be 'na' if not started, otherwise
   * the numeric duration in milliseconds.
   */
  get value() {
    if (this.discreteValue != null) {
      return this.discreteValue
    } else {
      return this.ended ? this.duration : NOT_ENDED_VALUE
    }
  }
}

/**
 * A higher order function that wraps the provided function in a request context with user
 * and internal timings reset
 * @private
 * @param handler
 */
export function withTimings<T extends Promise<any>>(handler: (...args: any[]) => T) {
  return (...args: any[]) => {
    return withContext(() => {
      return handler(...args)
    })
  }
}

/**
 * Returns the value of the internal timing with the specified name
 * @private
 * @param name The name of an internal timing, for example "ecc"
 */
export function getInternalTiming(name: string) {
  return internalTimings().find(t => t.name === name)?.value
}

/**
 * Returns the internal timings from the current request context
 * @private
 */
function internalTimings(): Timing[] {
  return <InternalTiming[]>(<unknown>get('internalTimings', []))
}

/**
 * Returns the user timings from the current request context
 * @private
 */
function userTimings(): Timing[] {
  return <Timing[]>(<unknown>get('userTimings', []))
}

/**
 * Adds x-edg-t, x-edg-user-t, and server-timing headers to the response
 * @private
 * @param response
 */
export function addTimings(response: Response) {
  const userT = userTimings()
  const internalT = internalTimings()

  if (userT.length) {
    response.setHeader(HTTP_HEADERS.xEdgeUserT, getTimingsResponseHeader(userT))
    response.setHeader(HTTP_HEADERS.serverTiming, appendServerTimings(response))
  }
  if (internalT.length) {
    response.setHeader(HTTP_HEADERS.xEdgeT, getTimingsResponseHeader(internalT))
  }
}

/**
 * Gets the value of the x-edg-user-t header.
 * @private
 */
function getTimingsResponseHeader(timings: Timing[]) {
  return timings.map(t => `${t.name}=${t.value}`).join(',')
}

/**
 * Returns the user timings in server-timing response header format
 * @private
 */
function appendServerTimings(response: Response) {
  const existingHeader = response.getHeader(HTTP_HEADERS.serverTiming)
  const timings = userTimings().map(t => `${t.name};dur=${t.value}`)

  if (Array.isArray(existingHeader)) {
    timings.push(...existingHeader)
  } else if (existingHeader) {
    timings.push(existingHeader as string)
  }

  return timings.join(', ')
}

/**
 * Allows you to track the performance of your code and asynchronous operations such as fetches.
 * Timings will be returned as a comma delimited list in the `x-edg-user-t` header.
 *
 * ** Example **
 *
 * ```js
 * import Timing from '@edgio/core/timing'
 *
 * const timing = new Timing('fetch').start()
 * const result = await fetch('http://my-api.com/products/1')
 * timing.end() // will result in `x-edg-user-t: fetch=(millis)` response header
 * ```
 *
 * If a timing is started but not finished by the time the response is sent, the value will be `na`.
 */
export default class Timing extends InternalTiming {
  constructor(name: string) {
    super(name, false)
  }
}

/**
 * Names for x-edg-t components
 */
export enum InternalNames {
  edgeCache = 'ecc',
  transformRequest = 'wa',
  transformResponse = 'wz',
  routeMatching = 'wr',
  proxying = 'wp',
}

/**
 * Values for edgeCache component of x-edg-t
 */
export enum CacheValue {
  hit = 'hit',
  pass = 'pass',
  cached = 'cached',
}
