import log from '../log'
import EarlyReturn from './EarlyReturn'
import HandlerFinished from './HandlerFinished'
import Phase from './Phase'

export default async function executePlan(plan: any) {
  try {
    for (let step of plan) {
      try {
        for (let mod of step.modules) {
          const start = new Date().getTime()
          try {
            await mod.execute(step.phase)
          } finally {
            log.trace(
              `executed ${Phase[step.phase]}: ${mod.constructor.name} in ${
                new Date().getTime() - start
              }ms`
            )
          }
        }
      } catch (e) {
        if (!(e instanceof HandlerFinished)) {
          throw e
        }
      }
    }
  } catch (e) {
    if (!(e instanceof EarlyReturn)) {
      throw e
    }
  }
}
