/**
 * Marker class - wraps string to be matched literally, without using path-to-regexp mapping
 */
export class ExactPath {
  constructor(public value: string) {}

  toString() {
    return this.value
  }
}

/**
 * Marks path to be matched literally, without using path-to-regexp mapping
 * @param path
 * @returns
 */
export default function exact(path: string): ExactPath {
  return new ExactPath(path)
}
