/**
 * Gets the directory corresponding to the root of the project source code.
 */
export function getSourceDir() {
  return process.env.EDGIO_ROOT_SOURCE_DIR || process.cwd()
}

/**
 * Sets the directory corresponding to the root of the project source code.
 * @param dir The directory to set
 */
export function setSourceDir(dir: string) {
  process.env.EDGIO_ROOT_SOURCE_DIR = dir
}
