import { Origins } from '../../types'
import Request from '../Request'

/// This class is used to pass data from the main thread to the worker thread.
/// Everything in it must be serializable by v8 so that it can be serialized.
export default class EdgeFunctionWorkerParams {
  readonly request: any
  readonly path: string
  readonly sysVars: Map<string, string | Uint8Array>
  readonly envVars: Map<string, string>
  readonly usrVars: Map<string, string>
  readonly httpVars: Map<string, string>
  readonly origins: Origins[]

  constructor(
    request: Request,
    rawBody: Buffer | undefined,
    path: string,
    sysVars: Map<string, string | Uint8Array>,
    envVars: Map<string, string>,
    usrVars: Map<string, string>,
    httpVars: Map<string, string>,
    origins: Origins[]
  ) {
    this.request = {
      httpVersion: request?.httpVersion,
      method: request?.method,
      rawBody,
      rawHeaders: request?.rawHeaders,
      url: `${request?.protocol}://${request?.headers['host']}${request?.url}`,
    }
    this.path = path
    this.sysVars = sysVars
    this.envVars = envVars
    this.usrVars = usrVars
    this.httpVars = httpVars
    this.origins = origins
  }
}
