import CookieOptions from '../router/CookieOptions'
/**
 * Serializes a cookie name with value and its options into a string
 * format of set-cookie header.
 * @param name
 * @param value
 * @param options
 * @example serializeCookie('name', 'value', { domain: 'test.com', HttpOnly: true, Secure: true }) => 'name=value; domain=test.com; HttpOnly; Secure'
 */
export function serializeCookie(name: string, value: string, options: CookieOptions = {}): string {
  return Object.keys(options).reduce((output, key) => {
    const opt = options[key as keyof CookieOptions]
    if (typeof opt === 'boolean' && opt) return `${output}; ${key}`
    return `${output}; ${key}=${opt}`
  }, `${name}=${value}`)
}
