/**
 * Error thrown when a route pattern cannot be parsed by path-to-regexp
 */
export default class InvalidRoutePatternError extends Error {
  /**
   * Indicates that the error is user error and that it should be handled as so.
   */
  public readonly isUserError: boolean

  constructor(pattern: string) {
    super(
      `Route pattern "${pattern}" contains invalid syntax. To force exact matching, wrap the path in the 'exact' function.\n` +
        `See https://docs.edg.io/guides/performance/cdn_as_code/common_routing_patterns for more information.`
    ) // istanbul ignore next - this is a known issue with TypeScript
    this.isUserError = true
  }
}
