import RulesVisitor, { RulesVisitorCallbacks } from './RulesVisitor'
import { Boolean, Conditionals, RulesVariables } from '../../types'
import {
  EQUALS,
  EQUALS_EXPRESS,
  GREATER_THAN,
  GREATER_THAN_OR_EQUALS,
  IN,
  LESS_THAN,
  LESS_THAN_OR_EQUALS,
  MATCHES,
  NOT_EQUALS,
  NOT_EQUALS_EXPRESS,
  NOT_IN,
  NOT_MATCHES,
  OR,
  AND,
  Operand,
} from './constants'
import toRegExp from '../toRegExp'
import toPathRegexp from '../../utils/toPathRegexp'

/**
 * This operator map provides the functions which are used to evaluate the conditionals.
 * The key is the operator name, the value is the function which accepts two operands (left and right side of condition).
 */
export const operatorsMap: { [key: string]: (l: any, r: any) => boolean } = {
  [EQUALS]: (l: any, r: any) => l === r,
  [NOT_EQUALS]: (l: any, r: any) => l !== r,
  [EQUALS_EXPRESS]: (l: any, r: any) => toPathRegexp(<string>r).test(l?.toString() ?? ''),
  [NOT_EQUALS_EXPRESS]: (l: any, r: any) => !toPathRegexp(<string>r).test(l?.toString() ?? ''),
  [LESS_THAN]: (l: any, r: any) => Number(l) < Number(r),
  [LESS_THAN_OR_EQUALS]: (l: any, r: any) => Number(l) <= Number(r),
  [GREATER_THAN]: (l: any, r: any) => Number(l) > Number(r),
  [GREATER_THAN_OR_EQUALS]: (l: any, r: any) => Number(l) >= Number(r),
  [MATCHES]: (l: any, r: any) => toRegExp(<string>r).test(l?.toString() ?? ''),
  [NOT_MATCHES]: (l: any, r: any) => !toRegExp(<string>r).test(l?.toString() ?? ''),
  [IN]: (l: any, r: any) => r?.some((item: string | number) => l?.toString() === item?.toString()),
  [NOT_IN]: (l: any, r: any) =>
    !r?.some((item: string | number) => l?.toString() === item?.toString()),
}

/**
 * This callback should accept RulesVariables and return the values for them.
 * If the variable is unknown or not supported, it should return undefined.
 */
export type EvalVariableCallback = (variable: RulesVariables) => Operand | undefined

/**
 * Visitor for the Rules which visits each node of the Rules structure
 * and call callbacks for each node type only if the Rule's condition is evaluated to true.
 */
export default class RulesConditionalVisitor extends RulesVisitor {
  // The evalVariableCallback provides condition variables
  protected evalVariableCallback: EvalVariableCallback

  constructor(evalVariableCallback: EvalVariableCallback, callbacks: RulesVisitorCallbacks = {}) {
    super(callbacks)
    this.evalVariableCallback = evalVariableCallback
  }

  /**
   * This method is called when we visit Matches node.
   * It calls visitFeatures/visitRules if the Matches condition is evaluated to true.
   * @param matches
   * @param ruleIndex
   */
  protected visitMatches(matches: any, ruleIndex: number) {
    this.callbacks?.onMatchesVisit?.(matches, ruleIndex)

    const ifStatement = matches?.if
    if (!ifStatement) return

    let conditionIndex = 0
    while (conditionIndex < matches.if.length) {
      const ifIndex = conditionIndex
      const thenIndex = conditionIndex + 1
      const elseIndex = conditionIndex + 2
      const isLastCondition = elseIndex === matches.if.length - 1

      const condition = ifStatement[ifIndex]
      let conditionResult = false

      // Evaluate the condition by evaluating the nested nodes
      if (this.isConditionals(condition)) {
        conditionResult = this.evalConditionals(condition, ruleIndex)
      } else if (this.isBoolean(condition)) {
        conditionResult = this.evalBoolean(condition, ruleIndex)
      } else {
        throw new Error(`Unsupported operator "${Object.keys(condition).pop()}".`)
      }

      // If the condition is met, we visit the 'then' Features,
      // otherwise we visit the last 'else' Features.
      if (conditionResult) {
        this.visitFeaturesOrRules(ifStatement[thenIndex], ruleIndex)
      } else if (isLastCondition) {
        this.visitFeaturesOrRules(ifStatement[elseIndex], ruleIndex)
      }

      // We're done if this is the last Condition in Rule
      if (isLastCondition) {
        break
      }

      // If the condition isn't last, we have an 'else-if' statement.
      // We update the index to point to the 'if' statement of it, and redo the loop.
      conditionIndex += 2
    }
  }

  /**
   * Returns true if provided Boolean is evaluated to true.
   * @param boolean
   * @param ruleIndex
   * @example
   * evalConditionals({
   *   "and": [
   *     {
   *       "==": [
   *         {
   *           "request": "path"
   *         },
   *         "/:path"
   *       ]
   *     },
   *     {
   *       "or": [
   *           {
   *              "==": [
   *                {
   *                  "request": "path"
   *                },
   *                "/:path"
   *              ]
   *           }
   *       ]
   *     }
   *   ]
   * }) => true
   */
  protected evalBoolean(boolean: Boolean, ruleIndex: number): boolean {
    this.callbacks?.onBooleanVisit?.(boolean, ruleIndex)

    const booleanItemHandler = (item: Conditionals | Boolean) => {
      if (this.isBoolean(item)) return this.evalBoolean(item, ruleIndex)
      return this.evalConditionals(item, ruleIndex)
    }
    if (boolean[AND]) {
      return boolean[AND].every(booleanItemHandler)
    }
    if (boolean[OR]) {
      return boolean[OR].some(booleanItemHandler)
    }
    return false
  }

  /**
   * Returns true if provided Conditionals is met, false otherwise.
   * @param conditionals
   * @param ruleIndex
   * @example
   * evalConditionals({
   *     "==": [
   *        {
   *          "request": "path"
   *        },
   *        "/:path"
   *     ]
   * }) => true
   */
  protected evalConditionals(conditionals: Conditionals, ruleIndex: number): boolean {
    this.callbacks?.onConditionalsVisit?.(conditionals, ruleIndex)

    const operators = Object.keys(conditionals)
    if (operators.length !== 1) {
      throw new Error(
        `Conditional statements must contain a single operator. The following operators were found: ${operators.join(
          ', '
        )}`
      )
    }

    const [operator, condition] = Object.entries(conditionals)[0]
    return this.evalCondition(condition, operator, ruleIndex)
  }

  /**
   * Returns true if provided Condition is met, false otherwise.
   * @param condition
   * @param operator
   * @param ruleIndex
   * @example
   * evalCondition([
   *     {
   *          "request": "path"
   *     },
   *     "/:path"
   * ], "==") => true
   */
  protected evalCondition(condition: any[], operator: string, ruleIndex: number): boolean {
    this.callbacks?.onConditionVisit?.(condition, operator, ruleIndex)

    const [left, right] = condition.map((operand: any) => this.evalOperand(operand))
    return operatorsMap?.[operator]?.(left, right)
  }

  /**
   * Evaluates an operands, which may be a constant or RulesVariable instance.
   * @param operand
   * @returns
   */
  protected evalOperand(operand: Operand): Operand | undefined {
    if (
      operand == null ||
      typeof operand === 'string' ||
      typeof operand === 'boolean' ||
      typeof operand === 'number' ||
      Array.isArray(operand)
    ) {
      return operand
    }
    return this.evalVariableCallback(<RulesVariables>operand)
  }
}
