/**
 * Escapes a string to be compatible with a RegExp
 * @param string
 * @returns {String}
 */
export default function regExpEscape(string: string): string {
  return string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&') // $& means the whole matched string
}

/**
 * Converts the given string into a RegExp
 * @param string
 * @returns {RegExp}
 */
export function toRegExp(string: string): RegExp {
  return new RegExp(regExpEscape(string))
}
