/* istanbul ignore file - For some reason the constructor can't get 100% coverage on the super call */
/**
 * To be thrown when failing to connect to a backend.
 */
export default class BackendFetchError extends Error {
  readonly cause: Error
  readonly type = 'BackendFetchError'

  /**
   * @param cause The root error emitted by http/https.request
   */
  constructor(cause: Error) {
    super(cause.message)
    this.cause = cause
  }
}
