import * as uuid from 'uuid'

/**
 * Objects of this class represent a single instantiation of an AWS Lambda function.
 */
export default class LambdaInstance {
  private readonly _id = uuid.v4().toString()
  private readonly _spawnedAt = Date.now()
  private _invocationCounter = 0
  private _lifetimeDuration = 0

  public get id() {
    return this._id
  }

  public get age() {
    return Date.now() - this._spawnedAt
  }

  public get invocationCounter() {
    return this._invocationCounter
  }

  public get lifetimeDuration() {
    return this._lifetimeDuration
  }

  public startInvocation() {
    ++this._invocationCounter
  }

  public stopInvocation(duration: number) {
    this._lifetimeDuration += duration
  }
}

/**
 * Objects of this class represent a single invocation of an AWS Lambda function.
 */
export class LambdaInvocation {
  private readonly _startTime = Date.now()
  private _stopTime: number
  private readonly _lambda: LambdaInstance
  private _isRunning = false

  public get lambda() {
    return this._lambda
  }

  public get duration() {
    return this._stopTime - this._startTime
  }

  constructor(lambda: LambdaInstance) {
    this._lambda = lambda
    this._stopTime = this._startTime
  }

  start() {
    if (!this._isRunning) {
      this._isRunning = true
      this._lambda.startInvocation()
    }
  }

  stop() {
    if (this._isRunning) {
      this._isRunning = false
      this._stopTime = Date.now()
      this._lambda.stopInvocation(this.duration)
    }
  }
}
